.navbar{
  background-color: var(--text);

  a{
    color: var(--background);
    &:hover{
      color: var(--primary-color-base);
    }
    &:active{
      color: var(--primary-color-base);
    }
  }

  &_bg{
    &_1{
      background-color: var(--text);

      a{
        color: var(--background);
        &:hover{
          color: var(--primary-color-base);
        }
        &:active{
          color: var(--primary-color-base);
        }
    
      }

      .nav-link.active{
        color: var(--background);
        &:hover{
          color: var(--primary-color-base);
        }
        &:active{
          color: var(--primary-color-base);
        }
      }
    }

    &_2{
      background-color: var(--primary-color-base);

      a{
        color: var(--background);
        &:hover{
          color: var(--secondary-color-light);
        }
        &:active{
          color: var(--secondary-color-light);
        }
    
      }
      .nav-link.active{
        color: var(--background);
        &:hover{
          color: var(--secondary-color-light);
        }
        &:active{
          color: var(--secondary-color-light);
        }
      }
    }

    &_3{
      background-color: var(--secondary-color-base);

      a{
        color: var(--background);
        &:hover{
          color: var(--primary-color-dark);
        }
        &:active{
          color: var(--primary-color-dark);
        }
    
      }

      .nav-link.active{
        color: var(--background);
        &:hover{
          color: var(--primary-color-dark);
        }
        &:active{
          color: var(--primary-color-dark);
        }
      }
    }

    &_4{
      background-color: var(--white);

      a{
        color: var(--text);
        &:hover{
          color: var(--primary-color-base);
        }
        &:active{
          color: var(--primary-color-dark);
        }
    
      }

      .nav-link.active{
        color: var(--text);
        &:hover{
          color: var(--primary-color-base);
        }
        &:active{
          color: var(--primary-color-dark);
        }
      }
    }
  }

   //mobile navbar 
   &-collapse{
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    margin: auto;
    background-color: var(--text);
    padding: 20px 55px;
    @media screen and (min-width: 768px) {
     left: auto;
    }

    @media (min-width: 992px) {
      width: 100%;
      position: static;
      padding: 10px 0px;
      background-color: transparent;
    }
  }

  // togler
  &-toggler {
    position: absolute;
    right: 25px;
    border: none;
    background: transparent !important;
    padding: 0;

    .icon-bar {
      width: 22px;
      height: 2px;
      background-color: var(--background);
      display: block;
      transition: all 0.2s;
      margin-top: 4px
    }

    &:focus{
      outline: none;
      border: none;
      box-shadow: none;
    }

    .top-bar {
      transform: rotate(45deg);
      transform-origin: 10% 10%;
    }

    .middle-bar {
      opacity: 0;
    }

    .bottom-bar {
      transform: rotate(-45deg);
      transform-origin: 10% 90%;
    }

    &.collapsed .top-bar {
      transform: rotate(0);
    }

    &.collapsed .middle-bar {
      opacity: 1;
    }

    &.collapsed .bottom-bar {
      transform: rotate(0);
    }

  }
}