html{
  font-size: 14px;

  @media (min-width: 768px) {
    font-size: 16px;
  }
}

body{
  background-color: var(--background);
  color: var(--text);
}

.color-preview {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 100px;
}

.color-preview-item {
  width: 30px;
  height: 30px;
  margin: 5px;
  border-radius: 50%;
  background-color: #000000;
  border: 1px solid #000;
}

.ql-align-center {
  text-align: center;
}

.ql-align-right {
  text-align: right;
}

.ql-align-justify {
  text-align: justify;
}

.ql-size-small {
  font-size: 0.75em;
}

.ql-size-large {
  font-size: 1.5em;
}

.ql-size-huge {
  font-size: 2.5em;
}

.ql-editor { min-height: 150px }

.footerImg{
  max-height: 100px;
}

.navbarImg{
  max-height: 50px;
}